import React, { useState, useEffect } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import Grid from '@material-ui/core/Grid';
import GridList from '@material-ui/core/GridList';
import GridListTile from '@material-ui/core/GridListTile';
import Avatar from '@material-ui/core/Avatar';
import CardContent from '@material-ui/core/CardContent';
import Typography from '@material-ui/core/Typography';

import { Card } from '@material-ui/core';
import Skills from './components/skills';
import Languages from './components/languages';
import Educations from './components/educations';
import Experiences from './components/experiences';
import Projects from './components/projects';
import Contacts from './components/contact';

const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
    flexWrap: 'wrap',
    justifyContent: 'space-around',
    overflow: 'hidden',
    paddingTop: '20px',
    backgroundColor: theme.palette.background.paper,
  },
  section: {
    height: 'auto !important',
    width: '100% !important',
  },
  sectionTitle: {
    margin: '10px 0px 5px 0px',
  },
  avatar: {
    width: theme.spacing(20),
    height: theme.spacing(20),
  },
  title: {
    textAlign: 'center',
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'center',
  },
  gridList: {
    padding: '15px',
  },
}));

export default function CV() {
  const classes = useStyles();
  const [profile, setProfile] = useState({
    summary: '',
    contacts: [],
    languages: [],
    skills: [],
    educations: [],
    experiences: [],
    projects: [],
  });
  useEffect(() => {
    import('./data').then((result) => {
      setProfile(result.default);
    });
  }, []);

  return (
    <Container className={classes.root}>
      <GridList className="header">
        <GridListTile key={profile.avatar} className={classes.section}>
          <Grid container>
            <Grid item sm={4}>
              <Avatar
                className={classes.avatar}
                alt={profile.name}
                src={process.env.PUBLIC_URL + profile.avatar}
              />
            </Grid>
            <Grid item sm={8} className={classes.title}>
              <CardContent>
                <Typography gutterBottom variant="h5" component="h2">
                  {profile.name}
                </Typography>
                <Typography variant="body2" color="textSecondary" component="p">
                  {profile.title}
                </Typography>
              </CardContent>
            </Grid>
          </Grid>
          <Grid container />
        </GridListTile>
        <GridListTile key="sumary" className={classes.section}>
          <h2 className={classes.sectionTitle}>SUMARY</h2>
          <Card>
            {profile.summary.split('\n').map((line) => (
              <Typography style={{ marginLeft: '5px' }}>{line}</Typography>
            ))}
          </Card>
        </GridListTile>
        <GridListTile key="contact" className={classes.section}>
          <h2 className={classes.sectionTitle}>CONTACT</h2>
          <Contacts contacts={profile.contacts} />
        </GridListTile>
        <GridListTile key="timeline" className={classes.section}>
          <h2 component="h1" className={classes.sectionTitle}>
            EXPERIENCES
          </h2>
          <Experiences experiences={profile.experiences} />
        </GridListTile>
        <GridListTile key="side_projects" className={classes.section}>
          <h2 component="h1" className={classes.sectionTitle}>
            SIDE PROJECTS
          </h2>
          <Projects projects={profile.projects} />
        </GridListTile>
        <GridListTile key="skills" className={classes.section}>
          <h2 component="h1" className={classes.sectionTitle}>
            SKILLS AND EXPERTISE
          </h2>
          <Skills skills={profile.skills} />
        </GridListTile>
        <GridListTile key="education" className={classes.section}>
          <h2 component="h1" className={classes.sectionTitle}>
            EDUCATION
          </h2>
          <Educations educations={profile.educations} />
        </GridListTile>
        <GridListTile key="languages" className={classes.section}>
          <h2 component="h1" className={classes.sectionTitle}>
            LANGUAGES
          </h2>
          <Languages languages={profile.languages} />
        </GridListTile>
      </GridList>
    </Container>
  );
}
