import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import Typography from '@material-ui/core/Typography';
import Paper from '@material-ui/core/Paper';
import Timeline from '@material-ui/lab/Timeline';
import TimelineItem from '@material-ui/lab/TimelineItem';
import TimelineSeparator from '@material-ui/lab/TimelineSeparator';
import TimelineConnector from '@material-ui/lab/TimelineConnector';
import TimelineContent from '@material-ui/lab/TimelineContent';
import TimelineDot from '@material-ui/lab/TimelineDot';

const useStyles = makeStyles((theme) => ({
  root: {
    width: '100%',
    maxWidth: 360,
    backgroundColor: theme.palette.background.paper,
  },
  timelineItem: {
    '&::before': {
      content: 'none',
    },
  },
  jobBlock: {
    padding: '15px',
  },
  jobTitle: {
    padding: '0px 0px',
    marginTop: '0px',
    marginBottom: '0px',
  },
  time: {
    margin: '5px 0px',
    color: 'rgb(175 170 170 / 87%)',
  },
  project: {
    paddingBottom: '10px',
  },
}));

export default function Skills({ experiences = [] }) {
  const classes = useStyles();
  return (
    <Timeline align="left">
      {experiences.map((experience) => (
        <TimelineItem className={classes.timelineItem}>
          <TimelineSeparator>
            <TimelineDot variant="outlined" color={experience.color} />
            <TimelineConnector />
          </TimelineSeparator>
          <TimelineContent>
            <Paper elevation={3} className={classes.jobBlock}>
              <h3 className={classes.jobTitle}>
                {experience.role} at {experience.company}
              </h3>
              <p className={classes.time}>
                {experience.startTime} - {experience.endTime || 'Now'}
              </p>
              {experience.projects.map((project) => (
                <Card className={classes.project} variant="outlined">
                  <CardContent>
                    <Typography
                      variant="h5"
                      component="h2"
                      className={classes.project}
                    >
                      {project.name}
                    </Typography>
                    <Typography className={classes.pos} color="textSecondary">
                      <b>Skills: </b>
                      {project.skills.join(', ')}
                    </Typography>
                    <Typography paragraph>
                      <b>Descriptions: </b> {project.description}
                    </Typography>
                    <Typography paragraph>
                      <b>Responsibility: </b> {project.role}
                    </Typography>
                  </CardContent>
                </Card>
              ))}
            </Paper>
          </TimelineContent>
        </TimelineItem>
      ))}
    </Timeline>
  );
}
