import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Avatar from '@material-ui/core/Avatar';
import { Box } from '@material-ui/core';
import CardHeader from '@material-ui/core/CardHeader';
import Card from '@material-ui/core/Card';
import Logo from '../images/dut.jpg';

const useStyles = makeStyles((theme) => ({
  root: {
    width: '100%',
    maxWidth: 360,
    backgroundColor: theme.palette.background.paper,
  },
  uniIcon: {
    width: '52px',
    height: '52px',
  },
}));

export default function Languages({ educations = [] }) {
  const classes = useStyles();
  return (
    <Box>
      {educations.map((education) => (
        <Card className={classes.education}>
          <CardHeader
            avatar={
              education.icon === 'DUT' ? (
                <Avatar src={Logo} className={classes.uniIcon} />
              ) : (
                <Avatar aria-label="recipe" className={classes.uniIcon}>
                  {education.icon}
                </Avatar>
              )
            }
            title={`${education.major} at ${education.school}`}
            subheader={`${education.qualification}. GPA: ${education.gpa}. Degree grade: ${education.grade} `}
          />
        </Card>
      ))}
    </Box>
  );
}
